@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input,textarea,button,select,a{-webkit-tap-highlight-color:rgba(0,0,0,0)}.App{text-align:center;font-family:'Sora', sans-serif}.App #quote-box{padding:30px;margin:15px;background-color:white;border-radius:15px}.App #quote-box #author{text-align:right}.App #quote-box .buttons{display:flex;justify-content:space-between}.App #quote-box .buttons #tweet-quote{display:flex;justify-content:center;align-items:center;min-width:25px;font-family:'Sora', sans-serif;border-radius:5px;color:white;text-decoration:none;padding:1%}.App #quote-box .buttons #tweet-quote:hover{opacity:0.9}.App #quote-box #new-quote{font-family:'Sora', sans-serif;font-size:0.6em;float:right;color:white;border-radius:5px;border:none;cursor:pointer;padding:10px}.App #quote-box #new-quote:hover{opacity:0.9}.App .App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}

